    justify-content: space-between;
    align-items: center;
    background-color: rgb(255,213,25);
    padding: 0.8rem 0.5rem;

}

.logo-nav {
    width: auto;
    height: 45px;
}


.right-side{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 60%;
    justify-content: space-around;
}

.candele {
    display: flex;
    flex-direction: column;
}

